import CreateSheets from './xlgenerator/spreadsheet';
import { getStyles, createWorkBookRel } from './xlgenerator/xlutil';
import JSZip from '../_internal/vendors/jszip/dist/jszip';
import { BLANKSTRING, UNDEF } from '@fusioncharts/core/src/lib';
/**
 * This function consumes data to be exported into xlsx in an array format
 * xlsx files are compressed form of multiple xml files
 * sample data[
 * ['JAN', 'FEB'],
 * ['10', '20'],
 * ['30', '40']
 * ]
 * Here the first row is header, the others are data
 * This function does two jobs
 * step- 1 Converst the data to required xml format for datasheets
 * step- 2 creates files using JSZip
 * step -3 returns a promise that compresses all the files and the zip is returned if promise is resolved
 * @param {Array} obj holds the data to be exported in array format
 * @returns Promise that holds the compressed data if promissed is resolved
 */
function exportJSONtoEXCEL (obj) {
  var sh = new CreateSheets(),
    zip,
    xl;
  sh.addSheet('Exported Data');
  let cellValue = BLANKSTRING;
  for (let rowindex = 0; rowindex < obj.length; rowindex++) {
    for (let columnindex = 0; columnindex < obj[rowindex].length; columnindex++) {
      cellValue = obj[rowindex][columnindex];
      if (typeof cellValue === 'object' && cellValue.value !== UNDEF) cellValue = cellValue.value;
      else if (cellValue !== UNDEF && cellValue.length);
      else cellValue = '';
      sh.createCell({ sheet: 0, row: rowindex, column: columnindex, value: cellValue });
    }
  }
  zip = new JSZip();
  zip.file('_rels/.rels', createWorkBookRel());
  xl = zip.folder('xl');
  xl.file('workbook.xml', sh.createWorkBook());
  xl.file('styles.xml', getStyles());
  xl.file('_rels/workbook.xml.rels', sh.createworkBookRels());
  zip.file('[Content_Types].xml', sh.createContentType());
  sh.fileSheets(xl);
  return zip.generateAsync({ type: 'blob', mimeType: 'application/vnd.ms-excel' });
}
export default {
  extension: exportJSONtoEXCEL,
  name: 'ExcelExport',
  type: 'extension',
  requiresFusionCharts: false
};
