import { toWorkBookRel, getWorkBookSheet, generateSheet } from './xlutil.js';

/**
 * CreateSheet class is designed and maintained for excel spreadsheet generation purpose. In this version basic spreadsheet
 * generation functions are provided.
 */
export default class CreateSheets {
  constructor () {
  // hols object for all sheets
    this.sheets = [];
  }
  /**
   * creates a new sheet for spreadtsheet
   * @param {string} sheetName new sheet name
   */
  addSheet (sheetName) {
    this.sheets.push({
      id: this.sheets.length + 1,
      rId: 'rId' + (3 + this.sheets.length),
      name: sheetName,
      rows: {}
    });
  }
  /**
   * This function adds cell object in sheet in specified row, column
   * @param {Object} cellObj object containing the cell information
   */
  createCell (cellObj) {
    if (!(cellObj.sheet === parseInt(cellObj.sheet, 10))) throw new Error('Invalid sheet number' + cellObj.sheet);
    if (!(cellObj.row === parseInt(cellObj.row, 10))) throw new Error('Invalid row number ' + cellObj.row + 'in sheet ' + cellObj.sheet);
    if (!(cellObj.column === parseInt(cellObj.column, 10))) throw new Error('Invalid column number ' + cellObj.column + 'in row ' + cellObj.row + ' in sheet ' + cellObj.sheet);

    let { sheet, row, column, value } = cellObj;

    if (this.sheets[sheet]) {
      if (this.sheets[sheet].rows[row]) {
        this.sheets[sheet].rows[row][column] = value;
      } else {
        this.sheets[sheet].rows[row] = { [column]: value };
      }
    } else {
      throw new Error(' Sheet' + sheet + ' does not exists');
    }
  }
  /**
   * creates workbook for the spreadsheet
   */
  createWorkBook () {
    let newsheet = '<?xml version="1.0" standalone="yes"?>' +
      '<workbook xmlns="http://schemas.openxmlformats.org/spreadsheetml/2006/main" xmlns:r="http://schemas.openxmlformats.org/officeDocument/2006/relationships">' +
    '<sheets>';
    for (let i = 0; i < this.sheets.length; i++) newsheet = newsheet + getWorkBookSheet(this.sheets[i]);
    return newsheet + '</sheets><calcPr/></workbook>';
  }
  /**
   * used to generate workbook specific styles
   */
  createworkBookRels () {
    let newsheet = `<?xml version="1.0" ?><Relationships xmlns="http://schemas.openxmlformats.org/package/2006/relationships">`;
    // rId2 is hardcoded and reserved for STYLES
    newsheet = newsheet + '<Relationship Id="rId2" Type="http://schemas.openxmlformats.org/officeDocument/2006/relationships/styles" Target="styles.xml"/>';
    for (let i = 0; i < this.sheets.length; i++) newsheet = newsheet + toWorkBookRel(this.sheets[i], i + 1);
    return newsheet + '</Relationships>';
  }
  /**
   * used to specify contenttype for each worksheets
   */
  createContentType () {
    let newsheet = `<?xml version="1.0" standalone="yes" ?><Types xmlns="http://schemas.openxmlformats.org/package/2006/content-types"><Default ContentType="application/xml" Extension="xml"/>`;
    newsheet = newsheet + '<Default ContentType="application/vnd.openxmlformats-package.relationships+xml" Extension="rels"/>';
    newsheet = newsheet + '<Override ContentType="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet.main+xml" PartName="/xl/workbook.xml"/>';
    newsheet = newsheet + '<Override ContentType="application/vnd.openxmlformats-officedocument.spreadsheetml.styles+xml" PartName="/xl/styles.xml" />';
    for (let i = 1; i <= this.sheets.length; i++) newsheet = newsheet + '<Override ContentType="application/vnd.openxmlformats-officedocument.spreadsheetml.worksheet+xml" PartName="/xl/worksheets/sheet' + i + '.xml"/>';
    return newsheet + '</Types>';
  }
  /**
   * creates folders/ files / files inside folders
   * @param {*} folder the container
   */
  fileSheets (folder) {
    for (let i = 0; i < this.sheets.length; i++) {
      folder.file('worksheets/sheet' + (i + 1) + '.xml', generateSheet(this.sheets[i]));
    }
  }
}
