// Basic template for sheet used to generate the worksheet
var templateSheet = '<?xml version="1.0" ?><worksheet xmlns="http://schemas.openxmlformats.org/spreadsheetml/2006/main" ' +
    'xmlns:mc="http://schemas.openxmlformats.org/markup-compatibility/2006" xmlns:mv="urn:schemas-microsoft-com:mac:vml" ' +
    'xmlns:mx="http://schemas.microsoft.com/office/mac/excel/2008/main" xmlns:r="http://schemas.openxmlformats.org/officeDocument/2006/relationships" ' +
    'xmlns:x14="http://schemas.microsoft.com/office/spreadsheetml/2009/9/main" xmlns:x14ac="http://schemas.microsoft.com/office/spreadsheetml/2009/9/ac" ' +
    'xmlns:xm="http://schemas.microsoft.com/office/excel/2006/main">' +
    '<sheetData>{rows}</sheetData></worksheet>';
/**
 * used to generate Relationship xml tags for each sheet
 * @param {Object} sheet sheet object
 * @param {integer} i sheet number
 */
export function toWorkBookRel (sheet, i) {
  return '<Relationship Id="' + sheet.rId + '" Target="worksheets/sheet' + i + '.xml" Type="http://schemas.openxmlformats.org/officeDocument/2006/relationships/worksheet"/>';
}
/**
 * Generates sheet xml tag for each sheets
 * @param {Object} sheet sheet object
 */
export function getWorkBookSheet (sheet) {
  return '<sheet state="visible" name="' + sheet.name + '" sheetId="' + sheet.id + '" r:id="' + sheet.rId + '"/>';
}
/**
 * It is a must have feature in xlsx file, styles must be provided to the sheets and workbooks
 */
export function getStyles () {
  return `<?xml version="1.0" encoding="utf-8"?>
    <styleSheet xmlns="http://schemas.openxmlformats.org/spreadsheetml/2006/main" xmlns:mc="http://schemas.openxmlformats.org/markup-compatibility/2006" mc:Ignorable="x14ac" xmlns:x14ac="http://schemas.microsoft.com/office/spreadsheetml/2009/9/ac">
    <numFmts count="0"></numFmts>
    <fonts count="2" x14ac:knownFonts="1" xmlns:x14ac="http://schemas.microsoft.com/office/spreadsheetml/2009/9/ac">
    <font><sz val="10" /><color rgb="FF333333" /><name val="Calibri Body" /></font>
    <font><b/><sz val="10" /><name val="Calibri Body" /></font>
    </fonts>
    <fills count="2">
    <fill><patternFill patternType="none"/></fill>
    <fill><patternFill patternType="gray125"/></fill>
    </fills>
    <borders count="1"><border><left /><right /><top /><bottom /><diagonal /></border></borders>
    <cellStyleXfs count="1"><xf numFmtId="0" fontId="0" fillId="0" borderId="0"/></cellStyleXfs>
    <cellXfs count="2">
    <xf numFmtId="0" fontId="1" fillId="0" borderId="0" xfId="0" applyAlignment="1">
      <alignment verticial="center" horizontal="center" />
    </xf>
    <xf numFmtId="undefined" fontId="0" fillId="undefined" borderId="undefined" xfId="0"  applyBorder="1"  applyFill="1"></xf>
    </cellXfs>
    <cellStyles count="1">
    <cellStyle name="Normal" xfId="0" builtinId="0"/>
    </cellStyles><dxfs count="0"/></styleSheet>`;
}
/**
 * The starting point for a sheet xml format generation, entire cells and rows are prepared and replaces the {rows}
 * string in templateSheet
 * @param {Object} sheet sheet object
 */
export function generateSheet (sheet) {
  return templateSheet.replace('{rows}', generateRows(sheet.rows));
}
/**
 * Generates Relationship xml tag for entire workbook
 */
export function createWorkBookRel () {
  let newsheet = `<?xml version="1.0" encoding="UTF-8" standalone="yes"?><Relationships xmlns="http://schemas.openxmlformats.org/package/2006/relationships">`;
  // rId1 is reserverd for WorkBook
  newsheet = newsheet + '<Relationship Id="rId1" Type="http://schemas.openxmlformats.org/officeDocument/2006/relationships/officeDocument" Target="xl/workbook.xml"/>';
  return newsheet + '</Relationships>';
}
/**
 * Calculates column value for the cell
 * @param {integer} i cell number
 */
function cellNameH (i) {
  let rest = Math.floor(i / 26) - 1,
    s = (rest > -1 ? cellNameH(rest) : ''); // eslint-disable-line good-practices/no-single-usage-variable
  return s + 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'.charAt(i % 26);
}
/**
 * calculates the cell identity eg. A3, BC24 etc where 'A' and 'BC' is the column number, 3, 24 is the row number
 * @param {integer} colIndex columnindex value for the cell
 * @param {integer} rowIndex rowindex value for the cell
 */
function cellName (colIndex, rowIndex) {
  return cellNameH(colIndex) + rowIndex;
};
function formatNumber (number) {
  return number.replace(/'/g, '').replace(/"/g, '');
}
/**
 *
 * @param {Object} columnData column specific information for a column
 * @param {*} column column index of a cell
 * @param {*} row row index of a cell
 */
function generateCell (columnData, column, row) {
  let cellData;
  if (row - 1 === 0) {
    cellData = '<c s="0" r="' + cellName(column, row) + '"';
  } else {
    cellData = '<c s="1" r="' + cellName(column, row) + '"';
  }
  if (isNaN(columnData)) {
    return cellData + ' t="inlineStr" ><is><t>' + checkSpecialChars(columnData) + '</t></is></c>';
  }
  return cellData + '><v>' + formatNumber(columnData) + '</v></c>';
}

function checkSpecialChars (specialStr) {
  let val = specialStr;
  if (typeof val !== 'string') val += '';

  return (val && RegExp(/[&<>"']/g.source).test(val)) ? replaceSpecialChars(val) : val;
}

function replaceSpecialChars (val) {
  let replacedVal = val;
  replacedVal = replacedVal.replace(/&/g, '&amp;')
    .replace(/</g, '&lt;')
    .replace(/>/g, '&gt;')
    .replace(/\"/g, '&quot;')
    .replace(/\'/g, '&#39;');
  return replacedVal;
}

function generateCells (rowData, row) {
  let cellObj = [];
  for (var column in rowData) {
    cellObj.push(generateCell(rowData[column], parseInt(column, 10), row));
  }
  return cellObj.join('');
}

function generateRow (rowData, row) {
  let xlRowVal = row + 1,
    xmlData = '<row r="' + xlRowVal + '">';
  xmlData = xmlData + generateCells(rowData, xlRowVal);
  return xmlData + '</row>';
}
function generateRows (rows) {
  let rowObj = [];
  for (var row in rows) {
    rowObj.push(generateRow(rows[row], parseInt(row, 10)));
  }
  return rowObj.join('');
}
